
import activityOutline from "./activityOutline";
import axios from "axios";
import api from "../api";
import util from "../util";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
activityOutline,

Flexbox,
FlexboxItem
},
name: "tab",
data() {
return {
activities: [
{










}
]
};
},
methods: {
get_activities() {

var _this = this;
axios
.get(api.get_acts) //这个api没换成推荐的API
.then(function(response) {
console.log(response.data);


_this.activities = response.data.data;

})
.catch(function(error) {
console.log(error);
});
},
to_activities() {
this.$router.push({ path: "/" });
},
to_setting() {
this.$router.push({ path: "/setting" });
}
},
mounted: function() {
this.get_activities();
},
created: function() {
util.getCode();
}
};



